import React from 'react';
import Image from "next/image";
import cat from "../../../../public/statics/cat.png"

const PromotionSection = () => {
    return (
        <section>
            <div className="w-full text-center">
                <h2 className="text-2xl md:text-3xl font-bold text-red-600 mb-4">АКЦИЯ</h2>
            </div>

            {/* Контейнер для изображения и кнопки с относительным позиционированием */}
            <div className="relative w-full">
                {/* Контейнер для кнопки с абсолютным позиционированием */}
                <div className="absolute inset-0 flex items-center px-90 top-90 z-10">
                    <button className="bg-red-600 hover:bg-red-700 text-white font-bold py-4 px-12 rounded-lg transition-colors shadow-lg hover:shadow-xl text-lg">
                        ЗАПИСАТЬСЯ
                    </button>
                </div>

                {/* Изображение */}
                <div className="relative w-full h-auto">
                    <Image
                        src={cat}
                        alt="котик"
                        className="w-full h-auto"
                    />
                </div>
            </div>
        </section>
    );
};

export default PromotionSection;